import React from 'react';
import Common from './Common';
import abtimg from '../src/images/about-image.png';

const About =() => {
    return (
        <>

            <div className="mt-5">
                <div className="text-center">
                    <h1>About Us</h1>
                </div>
            </div>
            <div className="about-main-div">
                <Common  name="Welcome To About Page" btname="Contact Now" visit="/contact" imgsrc={abtimg} />
            </div>

        </>
    );
}

export default About;