import React from 'react';
import { NavLink } from 'react-router-dom';

const Common =(props) => {
    return (
        <>

        <section id="header" className="d-flex align-items-center">
            <div className="container mx-auto">
                    <div className="row">
                        <div className="col-lg-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                            <h1>
                                {props.name}
                                <strong className="brand-name"> Nukind </strong> 
                            </h1>
                            <h2 className="py-3">
                                We are the team of talented developers making website
                            </h2>
                            <div className="mt-3">
                                <NavLink exact path to={props.visit} className="btn get-started"> {props.btname} </NavLink>
                            </div>
                        </div>

                        <div className="col-lg-6 order-1 order-lg-2 header-image">
                            <img src={props.imgsrc} className="img-fluid animated" alt="header image" />
                        </div>
                    </div>
            </div>
        </section>
        
        </>
    );
}

export default Common;