import React, { useState } from 'react';

const Contact =() => {


    const [data, setData] = useState({
        name: "",
        phone: "",
        email: "",
        message: "",
    });


    const InputEvent = (event) => {
        const { name, value } = event.target;

        setData((preVal) =>{
            return {
                ...preVal,
                [name]: value,
            };
        });
    };

    const formSubmit = (e) => {
        e.preventDefault();

        alert(
            `My name is ${data.name}, my mobile number is ${data.phone}, my mail id is ${data.email} and here is what i want to say ${data.message}.`
        );
    };


    return (
        <>

        <div className="my-5">
            <div className="text-center">
                <h1>
                    Contact US
                </h1>
            </div>
        </div>
        <div className="container mb-5">
            <div className="row">
                <div className="col-md-6 col-10 mx-auto">
                    <div className="contact-form">
                        
                        <form onSubmit={formSubmit}>
                            <input className="form-control mb-3" type="text" name="name" value={data.name} onChange={InputEvent} placeholder="Enter Your Name" />
                            <input className="form-control mb-3" type="phone" name="phone" value={data.phone} onChange={InputEvent} placeholder="Enter Phone Number" />
                            <input className="form-control mb-3" type="email" name="email" value={data.email} onChange={InputEvent} placeholder="Enter Gmail Id" />
                            <textarea className="form-control mb-3" type="text" rows="5" name="message" value={data.message} onChange={InputEvent} placeholder="Enter Message" />
    
                            <div className="mx-auto text-center form-button">
                                <button to="" className="btn submit-form-btn">Submit</button>
                            </div>
                        </form>

                    </div>
                </div>
            </div>
        </div>

        </>
    );
}

export default Contact;